import React, { useState, useEffect } from 'react';
import Modal from 'react-bootstrap/Modal';  
import { Button } from './Button';
import { Link } from 'react-router-dom';
import './Navbar.css';
import { auth } from "./../../firebase-config";
import { onAuthStateChanged, signOut } from 'firebase/auth';
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import imguser from './../../../src/componentes/Navbar/none.png';
import imgbutton from "./../Navbar/imgbutton.png";
import img1 from "./../Navbar/1.png";
import img2 from "./../Navbar/2.png";
import img3 from "./../Navbar/3.png";
import img4 from "./../Navbar/4.png";
import logotipo from "./../Navbar/logotipo2.png";

function Navbar() {
  const [dropdownOpen, setDropdownOpen] = React.useState(false);
  const toggle = () => setDropdownOpen(prevState => !prevState);
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  const [user,setUser] = useState({});
  const [acess, setAcess] = useState(false);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  onAuthStateChanged(auth,(currentUser)=>{
    setUser(currentUser);
    console.log(user);
})

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };
  
  const logout = async ()=>{
    await signOut(auth);
};

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener('resize', showButton);
  const [show, setShow] = useState(false);
  
  const handleClose = () => setShow(false);
  const handleShow = () =>{
    !acess?setShow(true):setShow(false); 
  } 
  

  return (
    <>
      <nav className='navbar-er'>
        
        <div className='navbar-container'>

          <Link to='/' className='navbar-logo' onClick={closeMobileMenu}>
            <img className='imgLogo' width={65} src={logotipo}/>
          </Link>

          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <button onClick={handleShow} id="MybtnModal" max-width={30} className='botaoAcessivel'><img className='imgA' width={100} src={imgbutton} /><br/><p className='txtA'>Opções de Acessibilidade</p></button>
              <Modal
            show={show}
            onHide={handleClose}
            backdrop="static"
            keyboard={false}
          >
            <Modal.Header closeButton>
              <Modal.Title>Opções de Acessibilidade</Modal.Title>
            </Modal.Header>
            <Modal.Body> <div className="divbotoesA" gap={2}>
              <button className='botoesA'><img className='botoesAimg' width={50} src={img1}/><br/><strong>Aumentar tamanho<br/> das fontes</strong></button>
              <button className='botoesA'><img className='botoesAimg' width={50} src={img2}/><br/><strong>Tradutor de Libras</strong></button><br/>
              <button className='botoesA'><img className='botoesAimg' width={50} src={img3}/><br/><strong>Leitor de Voz</strong></button>
              <button className='botoesA'><img className='botoesAimg' width={50} src={img4}/><br/><strong>Aumentar contraste</strong></button>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="primary" onClick={handleClose}>
                Fechar
              </Button>
              <Button variant="primary" onClick={handleClose}>
                Restaurar padrões
              </Button>
                
            </Modal.Footer>
          </Modal>
          

          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Home
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/projetos'
                className='nav-links'
                onClick={closeMobileMenu}
              >{}
                Projetos
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/contato'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Sobre nós
              </Link>
            </li>

            <li>
              <Link
                to='/Login'
                className='nav-links-mobile'
                onClick={closeMobileMenu}
              >
                Acessar
              </Link>
            </li>
          </ul>

          {
          user?
          <Dropdown isOpen={dropdownOpen} toggle={toggle}>
          <DropdownToggle caret> <p>{user.displayName?
          user.displayName
          :
          user.email}</p> 
          {user.photoURL?
          <img width="50" className='fotinha' max-height="20" src={user.photoURL}/> 
          : 
          <img className='fotinha' width="50" max-height="20" src={imguser}></img>
          }
          </DropdownToggle>
          <DropdownMenu>
            <Link  to='/Login'><DropdownItem>Configurações</DropdownItem></Link>
            <DropdownItem divider />
            <DropdownItem onClick={logout} >Sair</DropdownItem>
          </DropdownMenu>
        </Dropdown>
          :
          button && <Button buttonStyle='btn--outline'>ACESSAR</Button>
          }
        </div>  
      </nav>
    </>
  );
}

export default Navbar;